import React from 'react';
import { useStore } from '../../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import logo from '../../images/UI_Masketball_Logo.png'
import logomini from '../../images/UI_UTSouthwestern_Logo.png'

export const MainLogo = () => {
    const [mainLogoOn] = useStore((state) => [
        state.mainLogoOn,
    ]);

    const {spring} = useSpring({ 
        spring: mainLogoOn,
    })

    const y = spring.to([0, 1], ['translateY(-40%)', 'translateY(0%)'])
    const opacity = spring.to([0, 1], [0, 1])

    return(
        <animated.div  style={{position: 'absolute', left: '10vw', width: '80vw', top: '2vh', zIndex: '14', opacity: opacity, transform: y}} alt="splash screen" >
            <img src={logo} style={{width: '80vw'}} />
            <img src={logomini} style={{position: 'relative', width: '35vw', top: '-3vw', left: '50vw'}} />
        </animated.div>
    )
}