import React, { useEffect } from 'react';
import { useStore } from '../../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import readyButtonImg from '../../images/UI_Ready_Inactive.png'

export const ReadyButton = ({readyPressed}) => {
    const [readyButtonOn, setReadyButtonOn] = useStore((state) => [
        state.readyButtonOn,
        state.setReadyButtonOn
    ]);

    const {spring} = useSpring({ 
        spring: readyButtonOn,
    })

    const clicked = () => {
        setReadyButtonOn(0); 
        setTimeout(function() { 
            readyPressed();
        }, 200);
    }

    const y = spring.to([0, 1], ['translateY(60%)', 'translateY(0%)'])
    const opacity = spring.to([0, 1], [0, 1])

    return(
        <animated.img src={readyButtonImg} style={{position: 'absolute', zIndex: '15', bottom: '0', left: '15vw', width: '70%', opacity: opacity, transform: y}} alt="splash screen" onClick={()=>{ clicked(); }}/>
    )
}