  //           setEndVisibility(true);
  //         }, 2000);
  //       }
  //     }
  //     last = current;
  //   }, 100);
  // },[])

  let last = new Date();

  useInterval(
    () => {
      let current = new Date();
      let count = current - last;
      if(appState === appStates.PLAYING){
        setTimeLeft(timeLeft-count);
        if(timeLeft <= 100){
          setAppState(appStates.END);
          setGaugeRunning(false);
          setTimeLeft(0);
          turnOffCam();
          setTimeout(function() { 
            setEndVisibility(true);
          }, 2000);
        }
      }
    },
    100,
  );

