import React, { useState, useRef, useFrame } from 'react';
import * as THREE from 'three'
import { useStore } from '../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import useSound from 'use-sound';

import bgImg from '../images/splash_background_v3.jpg'
import fgImg from '../images/fgScroll.png'
import endScreen from '../images/UI_Results_BG_v4.png'
import thankyou from '../images/UI_Thanks.png'
import instruct1 from '../images/UI_Instruction1.png'
import instruct2 from '../images/UI_Instruction2.png'
import playAgain from '../images/UI_PlayAgain_Active.png'
import southwestWeb from '../images/UI_Southwestern_Active.png'
import takeSelfie from '../images/UI_TakeSelfie_Active.png'
import shareInactive from '../images/UI_Share_Inactive.png'
import loadingBall from '../images/UI_Basketball_Icon.png'
import loadingSpin from '../images/UI_Loading_Icon.png'
import loadingLogo from '../images/UI_LoadingScreen_Logo.png'
import camButton from '../images/UI_Camera_Inactive.png'
import logo from '../images/UI_Masketball_Logo.png'
import fgTop from '../images/splash_DarkBlue_v1.png'
import fgBottom from '../images/splash_LightBlue_v1.png'
import fgBottomWhite from '../images/splash_white_v1.png'
import selfieTop from '../images/UI_Selfie_TopBG.png'
import selfieBottom from '../images/UI_Selfie_BottomBG.png'

import launchSound from '../sounds/Launch_Button_1.mp3'
import gMusic from '../sounds/Game_Music_Loop_2.mp3'
import mMusic from '../sounds/Menu_Music_Loop_1.mp3'

import {takePhoto, share, clearState, renderPrediction} from '../facetrack'

import {LaunchButton} from './UI_Elements/LaunchButton'
import {MainLogo} from './UI_Elements/MainLogo'
import {IntroMavsman} from './UI_Elements/IntroMavsman'
import {IntroChamp} from './UI_Elements/IntroChamp'
import {ReadyButton} from './UI_Elements/ReadyButton'


const appStates= {
  SPLASH: 'splash',
  DIRECTIONS: 'directions',
  COUNTDOWN: 'countdown',
  PLAYING: 'playing',
  END: 'end',
  PHOTOTAKEN: 'phototaken'
}

export let cameraOn;
export function turnOffCam(){
  cameraOn = false;
}
cameraOn = false;

export const AppManager = (props) => {
  const [
    setAppState, 
    appState, 
    setTimerRunning, 
    resetTime, 
    resetPoints, 
    setGaugeRunning, 
    setCtdown, 
    endVisibility, 
    setEndVisibility, 
    points, 
    setLaunchButtonOn,
    setMainLogoOn,
    setMavsOn,
    setChampOn,
    setReadyButtonOn,
  ] = useStore((state) => [
      state.setAppState,
      state.appState,
      state.setTimerRunning,
      state.resetTime,
      state.resetPoints,
      state.setGaugeRunning,
      state.setCtdown,
      state.endVisibility,
      state.setEndVisibility,
      state.points,
      state.setLaunchButtonOn,
      state.setMainLogoOn,
      state.setMavsOn,
      state.setChampOn,
      state.setReadyButtonOn,
  ]);

  // const sound = new Audio(launchSound);
  // // const gameMusic = new Audio(gMusic);
  // const menuMusic = new Audio(mMusic);

  const [playLaunch, launchExp] = useSound(launchSound, {html5:true});
  const [playGameMusic, gameMusicExp] = useSound(gMusic, {html5:true});
  const [playMenuMusic, menuMusicExp] = useSound(mMusic, {html5:true});

  // React.useEffect(() => {
  //   window.addEventListener('touchstart', () => {
  //     sound.muted = false
  //     sound.play()
  //   })
  // })

  
  const [selfieMode, setSelfieMode] = useState(false)
  const [photoTaken, setPhotoTaken] = useState(false)
  const [launchScreenVis, setLaunchScreenVis] = useState(true)
  const [fgBottomScrolling, setFgBottomScrolling] = useState(false)

  const [inst1o, setInst1o] = useState(0)
  const [inst2o, setInst2o] = useState(0)
  // const [introO, setIntroO] = useState(1)
  const [bScrollZind, setBScrollZind] = useState(1)
  const [tScrollZind, setTScrollZind] = useState(11)

  const [loaded, setLoaded] = useState(false)
  const handleTrackingReady = (event) => {
    setLoaded(true);
  };
  React.useEffect(() => {
    document.addEventListener('trackingReady', handleTrackingReady);
    return () => {
      document.addEventListener('trackingReady', handleTrackingReady);
    };
  }, []);

  // const [instructScreenVis, setInstructScreenVis] = useState(true)
  // const [endScreenVis, setEndScreenVis] = useState("hidden")
  // const visref = useRef("visible");
  // const togvis = () => {
  //   console.log("click")
  //   if(test === "hidden"){setTest("visible")}
  //   else {setTest("hidden")}
  // }


  React.useEffect(() => {
    if(endVisibility){
      playMenuMusic();
      gameMusicExp.stop();
      setBScrollZind(11);
    }
  },[endVisibility])

  const isMounted = useRef(false);
  React.useEffect(() => {
    if (isMounted.current) {
      startIntro();
    } else {
      isMounted.current = true;
    }
  }, [loaded]);

  const startIntro = () => {
    setMavsOn(0);
    setChampOn(0);
    setLaunchButtonOn(0);
    setMainLogoOn(0);
    setTimeout(function() { 
      setMainLogoOn(1);
      setTimeout(function() { 
        setMavsOn(1);
        setTimeout(function() { 
          setChampOn(1);
          setTimeout(function() { 
            setLaunchButtonOn(1);
          }, 1000);
        }, 300);
      }, 300);
    }, 1000);
  }
  const handleLaunchPressed = () => {
    playLaunch();
    playMenuMusic();
    setTimeout(function() { 
      setAppState(appStates.DIRECTIONS);
      setMavsOn(0);
      setChampOn(0);
      setLaunchButtonOn(0);
      setTimeout(function() { 
        setInst1o(1);
        setTimeout(function() { 
          setInst2o(1);
          setTimeout(function() { 
            setReadyButtonOn(1);
          }, 2000);
        }, 2000);
      }, 1000);
    }, 300);
  }
  const handleReadyPressed = () => {
    playLaunch();
    menuMusicExp.stop();
    playGameMusic();
    startCountdown();
  }
  const startCountdown = () =>{
    setAppState(appStates.COUNTDOWN);
    setAppState(appStates.COUNTDOWN);
    setLaunchScreenVis(false);
    setFgBottomScrolling(true);
    cameraOn = true;
    renderPrediction();
    document.getElementById('webcam').style.zIndex = -1;
    setEndVisibility(false);
    setBScrollZind(1);
    resetTime();
    resetPoints();
    setTimeout(function() { 
      setCtdown('3');
      setTimeout(function() { 
        setCtdown('2');
        setTimeout(function() { 
          setCtdown('1');
          setTimeout(function() { 
            setCtdown('');
            startGame();
          }, 1000);
        }, 1000);
      }, 1000);
    }, 200);
  }
  const startGame = () =>{
    setAppState(appStates.PLAYING);
    setTimerRunning(true);
    setGaugeRunning(true);
  }
  const clearPhoto = () =>{
    setPhotoTaken(false);
    clearState();
  }
  return(
    <div>
      {!loaded && <div className="loadingscreen" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: 99999,}} >
        <img className="centered" src={loadingLogo} style={{width: '70vw', top: '80px', zIndex: '14'}} alt="splash screen" />
        <img className="centered" src={loadingBall} style={{width: '30%'}} alt="splash screen" />
        <img className="rotateme" src={loadingSpin}  alt="splash screen" />
        <div style={{fontFamily: 'FugazOne-Regular', textAlign: 'center', color: 'white', fontSize: '20px', position: 'absolute', bottom: '35%', left: '50%', transform: 'translate(-50%, 0)'}}>
          LOADING...
        </div>
      </div>}

      {launchScreenVis && <div className="launchscreen containeralt" style={{ zIndex: '12' }}>

        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '11', transform: 'translate(-50%,-50%) rotate(-40deg)'}}>
          <div style={{background: `url(${bgImg})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-10vh', left: '-30vh', width: '360vh', height: '120vh', zIndex: '12',}} />
        </div>
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '12', transform: 'translate(-50%,-50%) rotate(-40deg)'}}>
          <div className="sliding-background" style={{background: `url(${bgImg})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-10vh', left: '-30vh', width: '360vh', height: '120vh', zIndex: '12',}} />
        </div>
        
        <IntroMavsman />
        <IntroChamp />
        <MainLogo />
        <LaunchButton launchPressed={handleLaunchPressed} />
        <div style={{position: 'absolute', top: '50%', left: '50%', zIndex: '13', transform: 'translate(-50%,-50%) rotate(15deg)'}} >
          <img id="inst1" style={{position: 'relative', top: '5vw', left: '-2vw', width: '65vw', opacity: inst1o}} src={instruct1} alt="thank you" />
          <img id="inst2" style={{position: 'relative', top: '1vw', left: '4vw', width: '65vw', opacity: inst2o}} src={instruct2} alt="thank you" />
        </div>
        <ReadyButton readyPressed={handleReadyPressed} />
      </div>}

      {endVisibility && <div className="endscreen" >
        <img className="centered" src={endScreen} alt="splash screen" />

        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: tScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgTop})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-25vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div>
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: tScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgTop})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', top: '-25vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '12',}} />
        </div>
        
        <div style={{fontFamily: 'Elsner', fontSize: '30px', color: 'orange', position: 'absolute', top: '50%', left: '50%',  zIndex: '11'}}>
          <div style={{position: 'absolute', top: '-72vw', left: '-45vw', }} >
            SCORE
          </div>
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '80px', color: 'white', position: 'absolute', top: '50%', left: '50%',  zIndex: '11'}}>
          <div style={{position: 'absolute', top: '-76vw', left: '-22vw', }} >
            {(points<10)? '0'+points : points}
          </div>
        </div>
        <img className="centered" style={{top: '43%', width: '70vw', zIndex: '12'}} src={thankyou} alt="thank you" />
        <img className="centered" style={{top: '72%', width: '80vw', zIndex: '12'}} src={southwestWeb} onClick={()=>{window.location.href = "https://www.utsouthwestern.edu/";}} alt="thank you" />
        <img className="centered" style={{top: '82%', width: '80vw', zIndex: '12'}} src={playAgain} onClick={()=>{handleReadyPressed() }} alt="thank you" />
        <img className="centered" style={{top: '92%', width: '80vw', zIndex: '12'}} src={takeSelfie} onClick={()=>{document.getElementById('webcam').style.zIndex = 12; setSelfieMode(true); setTScrollZind(13)}} alt="thank you" />
        

      </div> }

      {fgBottomScrolling && <div>
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: bScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgBottom})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-75vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div>
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: bScrollZind, transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgBottom})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-70vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '12',}} />
        </div>
      </div>}

      {selfieMode && <div className="selfie-mode" >
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '13', transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div style={{background: `url(${fgBottomWhite})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-65vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '11',}} />
        </div>
        <div style={{ position: 'absolute', top: '50%', left: '50%', width: '100%', height: '100%', zIndex: '13', transform: 'translate(-50%,-50%) rotate(-15deg)'}}>
          <div className="sliding-foreground" style={{background: `url(${fgBottomWhite})`, backgroundSize: 'contain', backgroundRepeat: 'repeat-x', position: 'relative', bottom: '-60vh', left: '-30vh', width: '360vh', height: '60vh', zIndex: '12',}} />
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '30px', color: 'orange', position: 'absolute', top: '50%', left: '50%',  zIndex: '13'}}>
          <div style={{position: 'absolute', top: '-72vw', left: '-45vw', }} >
            SCORE
          </div>
        </div>
        <div style={{fontFamily: 'Elsner', fontSize: '80px', color: 'white', position: 'absolute', top: '50%', left: '50%',  zIndex: '13'}}>
          <div style={{position: 'absolute', top: '-76vw', left: '-22vw', }} >
            {(points<10)? '0'+points : points}
          </div>
        </div>
        <img id="logo" src={logo} style={{position: 'absolute', width: '80vw', bottom: '20vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" />
        <img id="selfietop" src={selfieTop} style={{display: 'none'}} alt="splash screen" />
        <img id="selfiebottom" src={selfieBottom} style={{display: 'none'}} alt="splash screen" />
        <img src={camButton} style={{position: 'absolute', width: '15vw', bottom: '3vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '14'}} alt="splash screen" onClick={()=>{takePhoto((points<10)? '0'+points : points.toString()), setPhotoTaken(true)}} />
      </div>}

      {photoTaken && <div className="photo-taken" style={{backgroundImage: 'linear-gradient(to bottom right, black, #004f91)', position: 'absolute', width: '100%', height: '100%', zIndex: '15',}} >
        <img id="image-preview" style={{position: 'absolute', zIndex: '16', width: '80vw', top: '5vw', left: '10vw'}}></img>
        <img src={shareInactive} style={{position: 'absolute', width: '60vw', bottom: '5vw', left: '50%', transform: 'translate(-12%, 0)', zIndex: '17'}} onClick={()=>{ share(); }} alt="thank you" />
        <img src={playAgain} style={{position: 'absolute', width: '80vw', bottom: '5vw', left: '50%', transform: 'translate(-75%, 0)', zIndex: '17'}} onClick={()=>{setSelfieMode(false); clearPhoto(); setTScrollZind(11); handleReadyPressed() }} alt="thank you" />
        <img src={takeSelfie} style={{position: 'absolute', width: '80vw', bottom: '22vw', left: '50%', transform: 'translate(-50%, 0)', zIndex: '17'}} onClick={()=>{clearPhoto();}} alt="thank you" />
      </div>}

    </div> 
  )
}