import React, { useEffect } from 'react';
import { useStore } from '../../hooks/useStore';
import {useSpring, animated} from 'react-spring'
import launchButtonImg from '../../images/UI_Launch_Inactive.png'

export const LaunchButton = ({launchPressed}) => {
    const [launchButtonOn, setLaunchButtonOn] = useStore((state) => [
        state.launchButtonOn,
        state.setLaunchButtonOn
    ]);

    const {spring} = useSpring({ 
        spring: launchButtonOn,
    })

    const clicked = () => {
        setLaunchButtonOn(0); 
        launchPressed();
    }

    const y = spring.to([0, 1], ['translateY(40%)', 'translateY(0%)'])
    const opacity = spring.to([0, 1], [0, 1])

    return(
        <animated.img src={launchButtonImg} style={{position: 'absolute', zIndex: '15', bottom: '0', left: '15vw', width: '70%', opacity: opacity, transform: y}} alt="splash screen" onClick={()=>{ clicked(); }}/>
    )
}
